import React, {useState} from 'react';
import {useSelector, useDispatch} from 'react-redux';

function CwPlayer(props) {

    const textToSend = useSelector((state) => state.settings.textToSend);
    const textToDisplay = useSelector((state) => state.settings.textToDisplay);
    const dispatch = useDispatch();

    const getDistanceFromTop = () => {
        try {
            return document.getElementById("divCwDisplay").offsetTop + 10;
        }
        catch (e){
            console.log('getDistanceFromTop', e);
            return 160;
        }
    };

    const style = {
        height: `calc(100vh - ${getDistanceFromTop()}px)`
    }


    return (
        <div id="divCwDisplay" style={style} className="flex mt-16 p-6 w-full rounded-md border-gray-600 bg-black text-yellow-400 text-9xl text-center border-4">
            <div className="m-auto">
                {textToDisplay}
            </div>
        </div>
    );
}

export default CwPlayer;