import { createSlice } from '@reduxjs/toolkit'
import { DISPLAY_MODES, MODES } from '../../../app/classes/constants';

// https://redux-toolkit.js.org/tutorials/quick-start

const initialState = {
    wpm: 20,
    farnsworthWpm: 5,
    display: DISPLAY_MODES.LINGERING.id,
    type: MODES.TEACH_ME_CW.id,
    textToSend: '',
    textToDisplay: '',
    isDrawerOpened : true,
    isAboutOpened : false,
    isPlaying: false,
    useLetters: true,
    useNumbers: true,
    useSymbols: true,
    totalCharRepeats: 3
}

export const settingsSlice = createSlice({
    name: 'settings',
    initialState,
    reducers: {
        setWpm: (state, action) => {
            state.wpm = action.payload;
            if(window.cwPlayer != null) window.cwPlayer.setWpm(state.wpm);
        },
        setFarnsworthWpm: (state, action) => {
            state.farnsworthWpm = action.payload;            
            if(window.cwPlayer != null) window.cwPlayer.setFarnsworthWpm(state.farnsworthWpm);
        },
        setDisplay: (state, action) => {
            state.display = action.payload;
            if(window.cwPlayer != null) window.cwPlayer.setDisplayMode(state.display);
        },
        setType: (state, action) => {
            state.type = action.payload;            
        },
        setTextToSend: (state, action) => {
            state.textToSend = action.payload;
        },
        setTextToDisplay: (state, action) => {
            state.textToDisplay = action.payload;
        },
        toggleDrawer : (state) => {
            state.isDrawerOpened = !state.isDrawerOpened;
        },
        togglePlaying : (state) => {
            state.isPlaying = !state.isPlaying;
        },
        toggleAbout : (state) => {
            state.isAboutOpened = !state.isAboutOpened;
        },
        setUseLetters: (state, action) => { 
            state.useLetters = action.payload;
            if(window.cwPlayer != null) window.cwPlayer.setUseLetters(state.useLetters);
        },
        setUseNumbers: (state, action) => { 
            state.useNumbers = action.payload;
            if(window.cwPlayer != null) window.cwPlayer.setUseNumbers(state.useNumbers);
        },
        setUseSymbols: (state, action) => { 
            state.useSymbols = action.payload;
            if(window.cwPlayer != null) window.cwPlayer.setUseSymbols(state.useSymbols);
        },
        setTotalCharRepeats: (state, action) => { 
            state.totalCharRepeats = action.payload;
            if(window.cwPlayer != null) window.cwPlayer.setTotalCharRepeats(state.totalCharRepeats);
        },

    },
})

export const {
    setWpm,
    setFarnsworthWpm,
    setDisplay,
    setType,
    setTextToSend,
    toggleDrawer,
    togglePlaying,
    setTextToDisplay,
    setUseLetters,
    setUseNumbers,
    setUseSymbols,
    setTotalCharRepeats,
    toggleAbout
} = settingsSlice.actions

export default settingsSlice.reducer