import TeachMeCWTrainer from './TeachMeCWTrainer';
import RandonCharsCWTrainer from './RandonCharsCWTrainer';
import { MODES } from './constants';

export default function createCwTrainer (className, wpm, 
    farnsworthWpm, useLetters, useNumbers, useSymbols, 
    totalCharRepeats, display, onDisplay) {

    switch (className) {
        case MODES.RANDOM_CHARACTERS.id:{
            return new RandonCharsCWTrainer(wpm, farnsworthWpm, useLetters, useNumbers, useSymbols, totalCharRepeats, display, onDisplay);
        }
        default : {
            return new TeachMeCWTrainer(wpm, farnsworthWpm, useLetters, useNumbers, useSymbols, totalCharRepeats, display, onDisplay);
        }
    }
}